import React from 'react'
import { Table, InputNumber, Form } from 'antd';
import './tableStyle.scss'

const EditableContext = React.createContext();

const EditableRow = ({ form, index, ...props }) => (
  <EditableContext.Provider value={form}>
    <tr {...props} />
  </EditableContext.Provider>
);

const EditableFormRow = Form.create()(EditableRow);

class EditableCell extends React.Component {
  state = {
    editing: false,
  };

  toggleEdit = () => {
    const editing = !this.state.editing;
    this.setState({ editing }, () => {
      if (editing) {
        this.input.focus();
        this.input.inputNumberRef.select();
      }
    });
  };

  save = e => {
    const { record, handleUpdateCell, dataIndex } = this.props;
    this.form.validateFields((error, values) => {
      if (error && error[e.currentTarget.id]) {
        return;
      }

      this.toggleEdit();

      if (record[dataIndex] !== values[dataIndex] && values[dataIndex] <= record.maxGrade) {
        handleUpdateCell({ ...record, ...values });
      }
    });
  };

  renderCell = form => {
    this.form = form;
    const { children, dataIndex, record } = this.props;
    const { editing } = this.state;
    if (!record.parentId) {
      return (
        <div className="static-cell">
          {children}
        </div>
      )
    }

    return editing ? (
      <Form.Item style={{ margin: 0 }}>
        {form.getFieldDecorator(dataIndex, {
          rules: [],
          initialValue: record[dataIndex],
        })(
          <InputNumber
            ref={node => { this.input = node }}
            style={{ width: '100%' }}
            min={0}
            max={record.maxGrade}
            onPressEnter={this.save}
            onBlur={this.save}
          />
        )}
      </Form.Item>
    ) : (
      <div
        className="editable-cell-value-wrap"
        onClick={this.toggleEdit}
        aria-hidden="true"
      >
        {children}
      </div>
    );
  };

  render () {
    const {
      editable,
      dataIndex,
      title,
      record,
      index,
      handleUpdateCell,
      children,
      ...restProps
    } = this.props;
    return (
      <td {...restProps}>
        {editable ? (
          <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>
        ) : (
          children
        )}
      </td>
    );
  }
}

export default (props) => {
  const { criteriaTypeId, dataSource, pagination, handleUpdateCell, handleTableChange } = props
  const columns = [
    {
      title: '',
      key: 'expand',
      width: '20px'
    },
    {
      title: 'Nội dung đánh giá',
      dataIndex: 'label',
      key: 'label',
      innerHtml: true
    },
  ];

  if (criteriaTypeId === 'TDG') {
    columns.push({
      title: 'SVHS',
      dataIndex: 'grade',
      key: 'grade',
      width: '10%',
      editable: true,
      align: 'right',
      className: 'numeric'
    })
  }
  else if (criteriaTypeId === 'LTDG') {
    columns.push({
      title: 'SVHS',
      dataIndex: 'grade',
      key: 'grade',
      width: '10%',
      editable: false,
      align: 'right',
      className: 'numeric'
    },
      {
        title: 'Lớp trưởng',
        dataIndex: 'monitorGrade',
        key: 'monitorGrade',
        width: '10%',
        editable: true,
        align: 'right',
        className: 'numeric'
      })
  }
  else if (criteriaTypeId === 'CVHT') {
    columns.push({
      title: 'Điểm',
      dataIndex: 'grade',
      key: 'grade',
      width: '10%',
      editable: false,
      align: 'right',
      className: 'numeric'
    },
      {
        title: 'Lớp trưởng',
        dataIndex: 'monitorGrade',
        key: 'monitorGrade',
        width: '10%',
        editable: false,
        align: 'right',
        className: 'numeric'
      },
      {
        title: 'CVHT',
        dataIndex: 'councelorGrade',
        key: 'councelorGrade',
        width: '10%',
        editable: true,
        align: 'right',
        className: 'numeric'
      })
  }

  const mappedColumns = columns.map(col => {
    const newCol = { ...col }
    if (newCol.innerHtml) {
      newCol.render = text => {
        return <div dangerouslySetInnerHTML={{ __html: text }} />;
      }
    }
    if (newCol.editable) {
      newCol.onCell = record => {
        return {
          record,
          editable: col.editable,
          dataIndex: col.dataIndex,
          title: col.title,
          handleUpdateCell
        }
      }
    }

    return newCol
  });

  const components = {
    body: {
      row: EditableFormRow,
      cell: EditableCell,
    },
  };

  return (
    <React.Fragment>
      <Table
        components={components}
        rowClassName={() => 'editable-row'}
        columns={mappedColumns}
        dataSource={dataSource}
        pagination={pagination}
        onChange={handleTableChange}
      />
    </React.Fragment>
  )
}


