import React, { useState, useEffect } from 'react'
import { Card, Row, Col, message, Button, Popconfirm, Icon } from 'antd';
import FormSearch from './FormSearch';
import { getTrainingScoreQuestionForTest, gradingTrainingScoreQuestion, lockTrainingScores } from '../../../services/studentPortal';
import useCurrentUserId from '../../../hook/useCurrentUserId';
import TableGradingTrainingScoreTesting from './TableGradingTrainingScoreTesting';
import CardTitle from '../../../components/common/CardTitle/index';

export default (props = {}) => {
  const { handleBack, hideSearchForm, hideLockButton } = props
  const criteriaTypeId = props.criteriaTypeId || 'TDG'
  const studentId = props.studentId || useCurrentUserId();
  const [yearEduId, setYearEduId] = useState(props.yearEduId);
  const [semesterId, setSemesterId] = useState(props.semesterId);
  const [trainingScoreQuestions, setTrainingScoreQuestions] = useState([]);
  const [dataChanged, setDataChanged] = useState(false);

  const handleClearTable = () => {
    setTrainingScoreQuestions([])
  }

  const callGetTrainingScoreQuestionForTest = async (params) => {
    setDataChanged(false)
    const [result, err] = await getTrainingScoreQuestionForTest({ ...params, studentId })
    if (err) {
      handleClearTable();
      return;
    }

    setTrainingScoreQuestions([...result.data.questions])
  }

  const callGradingTrainingScoreQuestion = async () => {
    const [, err] = await gradingTrainingScoreQuestion({ yearEduId, semesterId, studentId, criteriaTypeId, questions: trainingScoreQuestions })
    if (!err) {
      setDataChanged(false)
      message.success('Cập nhật thông tin thành công!');
    }
  }

  const callLockTrainingScores = async () => {
    await lockTrainingScores([{ yearEduId, semesterId, studentId, criteriaTypeId }])
    message.success('Cập nhật thông tin thành công!');
  }

  const onSearch = async (values) => {
    setYearEduId(values.yearEduId);
    setSemesterId(values.semesterId);
    await callGetTrainingScoreQuestionForTest(values);
  }

  const handleSave = async () => {
    await callGradingTrainingScoreQuestion()
  }

  const handleLock = async () => {
    await callLockTrainingScores()
  }

  const handleUpdateCell = (row) => {
    if (row) {
      const parentIndex = trainingScoreQuestions.findIndex(x => x.id === row.parentId)
      const parent = trainingScoreQuestions[parentIndex]
      const newParent = { ...parent }
      const newChildren = [...parent.children]
      const changedIndex = newChildren.findIndex(x => x.id === row.id)
      const child = newChildren[changedIndex]
      newChildren.splice(changedIndex, 1, { ...child, ...row })

      if (criteriaTypeId === 'TDG') {
        newParent.grade = newChildren.reduce((total, item) => total + item.grade, 0)
        if (newParent.grade > newParent.maxGrade) {
          return
        }
      }

      if (criteriaTypeId === 'LTDG') {
        newParent.monitorGrade = newChildren.reduce((total, item) => total + item.monitorGrade, 0)
        if (newParent.monitorGrade > newParent.maxGrade) {
          return
        }
      }

      if (criteriaTypeId === 'CVHT') {
        newParent.councelorGrade = newChildren.reduce((total, item) => total + item.councelorGrade, 0)
        if (newParent.councelorGrade > newParent.maxGrade) {
          return
        }
      }

      trainingScoreQuestions.splice(parentIndex, 1, { ...newParent, children: newChildren })

      setTrainingScoreQuestions([...trainingScoreQuestions])
      setDataChanged(true)
    }
  }

  useEffect(() => {
    if (yearEduId && semesterId && studentId) {
      callGetTrainingScoreQuestionForTest({ yearEduId, semesterId, studentId })
    }
  }, [yearEduId, semesterId, studentId])

  return (
    <Card className="self-grading-training-score-testing">
      <CardTitle title="Đánh giá rèn luyện" />
      {!hideSearchForm &&
        <Row>
          <Col span={24}>
            <FormSearch onSubmit={onSearch} />
          </Col>
        </Row>
      }
      <Row>
        <Col span={12}>
          {handleBack &&
            <Button
              icon="left"
              onClick={handleBack}
            >
              Trở lại
            </Button>
          }
        </Col>
        <Col span={12} style={{ textAlign: 'right' }}>
          <Button
            type="primary"
            icon="save"
            disabled={!dataChanged || !trainingScoreQuestions || trainingScoreQuestions.length === 0}
            onClick={handleSave}
          >
            Lưu
          </Button>&nbsp;
          {!hideLockButton &&
            <Popconfirm
              title="Bạn có chắc muốn khóa？"
              icon={<Icon type="question-circle-o" style={{ color: 'red' }} />}
              disabled={dataChanged || !trainingScoreQuestions || trainingScoreQuestions.length === 0}
              onConfirm={handleLock}
            >
              <Button
                type="danger"
                icon="lock"
                disabled={dataChanged || !trainingScoreQuestions || trainingScoreQuestions.length === 0}
                title={dataChanged ? "Lưu trước khi khóa." : ""}
              >
                Khóa
              </Button>
            </Popconfirm>
          }
        </Col>
      </Row>
      <Row>
        <Col span={24}>
          <TableGradingTrainingScoreTesting
            criteriaTypeId={criteriaTypeId}
            dataSource={trainingScoreQuestions}
            pagination={false}
            handleUpdateCell={handleUpdateCell}
            handleClearTable={handleClearTable}
          />
        </Col>
      </Row>
    </Card >
  )
}


